/* ***************************************** */
@media all and (min-width: 768px) {
  /* page layout for desktop screens */
  .page-header {
    height: 15em;
    position: relative;
    display: flex; /* on desktop flex-items are in columns */
    flex-direction: column;
    justify-content: space-evenly;
  }
  .page-header button.menu-btn {
    display: none;
  }
  .page-header .block.menu--main {
    display: flex;
    justify-content: center;
  }
  .page-footer .block.menu--footer {
    display: flex;
    justify-content: center;
  }
  .page-header ul {
    margin: 0 auto;
  }
  .utility-belt {
    margin-right: -7.568em;
    margin-left: -7.568em;
    padding-right: 7.568em;
    padding-left: 7.568em;
    display: block; /* show on desktop */
  }
  .mgr-links {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    align-content: space-between;
    flex-wrap: wrap;
  }
  .mgr-links > .block {
    /* remove margin from blocks in this region */
    margin-bottom: 0;
  }
  /* define child blocks within mgr-links -- assume managemenu and breadcrumb are not present */
  .mgr-links .block.menu--account {
    flex-basis: 100%;
    order: 2;
    display: flex;
    justify-content: flex-end;
  }
  /* breadcrumb is present - managemenu is not */
  .mgr-links.crumbs .block.menu--account {
    flex-basis: 28%;
    order: 2;
  }
  .mgr-links.crumbs .block.block-system-breadcrumb-block {
    flex-basis: 72%;
    order: 1;
  }
  /* managemenu is present */
  .mgr-links.manager nav.menu--manage,
  .mgr-links.manager .block.block-superfishmanage {
    width: auto;
    flex-basis: 72%;
    order: 1;
  }
  .mgr-links.manager nav.menu--account {
    flex-basis: 28%;
    order: 2;
    justify-content: flex-end;
  }
  .mgr-links.manager.crumbs .block.block-system-breadcrumb-block {
    order: 3;
    flex-basis: 100%;
  }
  /* define child blocks within mgr-links -- assume managemenu and breadcrumb are not present */
  .region.region-actions {
    margin-right: -7.568em;
    margin-left: -7.568em;
    padding-right: 7.568em;
    padding-left: 7.568em;
  }
  .region-highlighted {
    margin-right: -7.568em;
    margin-left: -7.568em;
    padding-right: 7.568em;
    padding-left: 7.568em;
  }
  /********************/
  /* /end site layout for desktop screens */
  /********************/
}
