/* Core button controls */
button,
.button a,
input.button,
a.button,
html input[type=button],
input[type=reset],
input[type=submit] {
  padding: 1em 1em 1em 1em;
  color: #ffffff;
  background: rgba(213, 179, 91, 0.8);
  border: 0.1em solid #333;
}
button:link, button:visited,
.button a:link,
.button a:visited,
input.button:link,
input.button:visited,
a.button:link,
a.button:visited,
html input[type=button]:link,
html input[type=button]:visited,
input[type=reset]:link,
input[type=reset]:visited,
input[type=submit]:link,
input[type=submit]:visited {
  text-decoration: none;
  color: #ffffff;
  background: rgba(213, 179, 91, 0.8);
  border: 0.1em solid #333;
}
button:hover, button:focus,
.button a:hover,
.button a:focus,
input.button:hover,
input.button:focus,
a.button:hover,
a.button:focus,
html input[type=button]:hover,
html input[type=button]:focus,
input[type=reset]:hover,
input[type=reset]:focus,
input[type=submit]:hover,
input[type=submit]:focus {
  color: #ffffff;
  background-color: #3ea0d8;
  border: 0.1em solid #333;
  outline: 0.025em solid rgba(255, 255, 255, 0.8);
  outline-offset: 0.01em;
}
button:active,
.button a:active,
input.button:active,
a.button:active,
html input[type=button]:active,
input[type=reset]:active,
input[type=submit]:active {
  transform: scale(0.97);
  background-color: #d5b35b;
}
button:last-child,
.button a:last-child,
input.button:last-child,
a.button:last-child,
html input[type=button]:last-child,
input[type=reset]:last-child,
input[type=submit]:last-child {
  margin-right: 0;
}

